h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: "Quicksand", sans-serif;
}

.profileImg {
  width: 100px;
  height: 100px;
}
.box {
  border: 2px solid rgb(22, 22, 22);
  border-radius: 8px;
}
.highlight {
  opacity: 0.8;
}
.highlight:hover {
  opacity: 1;
  cursor: pointer;
}

.num-box {
  transition: 0.2s;
  width: 9vh;
  height: 9vh;
  margin: 0.5vh;
  font-size: large;
  border-radius: 12px;
  box-shadow: 2px 2px 2px rgb(114, 114, 114);
}

.num-box:hover {
  transition: 0.2s;
  width: 10vh;
  height: 10vh;
  margin: 0vh;
  font-size: x-large;
  border-radius: 12px;
  box-shadow: 2px 2px 2px rgb(114, 114, 114),
    6px 6px 8px rgba(59, 59, 59, 0.452);
}
